/* 控制输入框按钮区域 */
.Sing2catModalControllZone {
  display: flex;
  justify-content: center;
  margin: 5px 0 5px 0;
}
/* 控制输入框按钮 */
.dark-Sing2catModalControllButton {
  background-color: dimgray;
  border-radius: 5px;
  border: 2px solid lightgray;
}
.Sing2catModalControllButton {
  background-color: azure;
  border-radius: 5px;
  border: 2px solid powderblue;
}
.Sing2catModalControllButtonSize {
  transition: 0.2s;
  margin: auto;
}
.Sing2catModalControllButtonSize:hover {
  transform: scale(1.1);
  cursor: pointer;
}
/* 配置文件信息输入框前缀 */
.Sing2catModalPrefixSize {
  font-size: 0.8rem;
  margin: 0;
}
/* 配置文件信息输入框 */
.Sing2catModalInput {
  background-color: rgb(233, 250, 250);

  border: 2px solid powderblue;
}
.dark-Sing2catModalInput {
  background-color: rgb(233, 250, 250);
  border: 2px solid lightgray;
}
.Sing2catModalInputSize {
  border-radius: 10px;
  padding-left: 5px;
}
.Sing2catModalInputSize:focus {
  outline: none;
  background-color: snow;
}
/* ruleset按钮 */
.dark-Sing2catRulesetButton {
  background-color: dimgray;
  border-radius: 5px;
  border: 2px solid lightgray;
  color: aliceblue;
}
.Sing2catRulesetButton {
  background-color: azure;
  border-radius: 5px;
  border: 2px solid powderblue;
}
.Sing2catRulesetButtonSize {
  margin: 1.2rem 5px 0 5px;
  height: 25px;
  transition: 0.2s;
}
.Sing2catRulesetButtonSize:hover {
  cursor: pointer;
  transform: scale(1.1);
}
/* 底部button */
.Sing2catModalFootButton {
  background-color: dodgerblue;
  border-radius: 5px;
  margin: 0 5px 0 5px;
  font-weight: bold;
  transition: 0.2s;
}
.Sing2catModalFootButton:hover {
  background-color: pink;
  cursor: pointer;
  transform: scale(1.2);
}
