.CollectNavBar {
  position: fixed;
  right: 2rem;
  top: 2.5rem;
  display: flex;
  flex-direction: column;
  height: 5rem;
  overflow-y: auto;
  padding-left: 5px;
  width: 5rem;
}
.CollectNavBar::-webkit-scrollbar {
  display: none;
}
.CollectNavEle {
  text-decoration: none;
  color: rgb(49, 49, 49);
  transition: 0.2s;
}
.dark-CollectNavEle {
  text-decoration: none;
  color: dimgray;
  transition: 0.2s;
}
.CollectNavEle:hover {
  color: lightblue;
  transform: scale(1.1);
}
.dark-CollectNavEle:hover {
  color: aliceblue;
  transform: scale(1.1);
}
.CollectButton {
  background-color: lightcyan;
  border: 2px solid powderblue;
  transition: 0.2s;
}
.dark-CollectButton {
  color: aliceblue;
  background-color: dimgrey;
  border: 2px solid lightgray;
  transition: 0.2s;
}
.CollectInput {
  border-bottom: 2px solid powderblue;
  border-top: 2px solid powderblue;
  border-left: 2px solid powderblue;
  background-color: azure;
}
.dark-CollectInput {
  border-bottom: 2px solid lightgray;
  border-top: 2px solid lightgray;
  border-left: 2px solid lightgray;
  background-color: lightgray;
}
.CollectGroupName {
  border-radius: 15px;
  text-align: center;
  width: 5rem;
  color: rgb(0, 160, 255);
  border: 2px solid rgb(0, 180, 255);
  border-bottom: 5px double rgb(0, 180, 255);
  font-size: 1.5rem;
  font-weight: bold;
  margin-left: 1rem;
}
.CollectGroupName:hover {
  cursor: pointer;
}
.dark-CollectGroupName {
  border-radius: 15px;
  text-align: center;
  width: 5rem;
  color: aliceblue;
  border: 2px solid aliceblue;
  border-bottom: 5px double aliceblue;
  font-size: 1.5rem;
  font-weight: bold;
  margin-left: 1rem;
}
.dark-CollectGroupName:hover {
  cursor: pointer;
}
.CollectCard {
  border: 2px solid powderblue;
  margin: 1rem 0.5rem 0.5rem 0.5rem;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding-left: 10px;
  background-color: azure;
  transition: 0.2s;
}
.CollectCard:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.dark-CollectCard {
  border: 2px solid lightgray;
  margin: 1rem 0.5rem 0.5rem 0.5rem;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding-left: 10px;
  background-color: dimgray;
  transition: 0.2s;
}
.dark-CollectCard:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.CollectCardHead {
  margin: -5px 0 0 5px;
  font-size: 1.2rem;
  color: cornflowerblue;
  font-weight: bold;
}
.CollectCardContent {
  margin: 0 0 0 5px;
  font-size: 0.8rem;
  color: lightgray;
}
.dark-CollectCardHead {
  margin: -5px 0 0 5px;
  font-size: 1.2rem;
  color: lightblue;
  font-weight: bold;
}
.dark-CollectCardContent {
  margin: 0 0 0 5px;
  font-size: 0.8rem;
  color: lightgray;
}
.CollectDelete {
  padding: 3px;
  border-radius: 5px;
  background-color: rgba(224, 255, 255, 0.2);
  backdrop-filter: blur(2px);
  position: absolute;
  bottom: -10px;
  right: 3px;
  transition: 0.2s;
}
.CollectDelete:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.CollectPop {
  background-color: azure;
  border-radius: 5px;
  width: 15rem;
}
.dark-CollectPop {
  background-color: dimgray;
  border-radius: 5px;
  width: 15rem;
}
@media screen and (min-width: 1201px) {
  #CollectInput {
    border-radius: 15px 0 0 15px;
    margin-top: 1%;
    margin-left: 5rem;
    font-size: 1.2rem;
    width: 20rem;
    border-right: none;
    height: 2rem;
    padding: 3px 3px 3px 10px;
  }
  #CollectButton {
    border-radius: 0 15px 15px 0;
    margin-top: 1%;
    font-size: 1.2rem;
    border-left: none;
    height: 2rem;
    padding: 3px;
  }
}
@media screen and (max-width: 1200px) and (min-width: 350px) {
  #CollectInput {
    border-radius: 15px 0 0 15px;
    margin-top: 5%;
    margin-left: 2rem;
    font-size: 1.2rem;
    border-right: none;
    height: 2rem;
    padding: 3px 3px 3px 10px;
    width: 8rem;
  }
  #CollectButton {
    border-radius: 0 15px 15px 0;
    margin-top: 5%;
    font-size: 1.2rem;
    border-left: none;
    height: 2rem;
    padding: 3px;
  }
}
#CollectButton:hover {
  cursor: pointer;
  font-size: 1.4rem;
}
#CollectInput:focus {
  outline: none;
  background-color: snow;
}
.CollectGroupContainer {
  width: 93%;
  margin-top: 1rem;
  padding: 10px;
  height: 10rem;
}
.CollectGroupContainer::-webkit-scrollbar {
  display: none;
}
#CollectCard {
  width: 10rem;
  height: 4rem;
  display: flex;
}
.CollectContainer::-webkit-scrollbar {
  display: none;
}
