.SiderContainer {
  display: flex;
  flex-direction: column;
  width: auto;
  height: 98%;
  padding: 10px;
  border-right: 1px solid rgba(105, 105, 105, 0.5);
  margin-top: 2px;
}
.dark-SiderContainer {
  display: flex;
  flex-direction: column;
  width: auto;
  height: 98%;
  padding: 10px;
  border-right: 1px solid rgba(211, 211, 211, 0.5);
  margin-top: 2px;
}
.dark-SiderButton {
  color: aliceblue;
  background-color: dimgray;
  border: 1px solid lightgray;
  box-shadow: 5px 5px 5px rgba(150, 150, 150, 0.5);
  border-radius: 5px;
  font-size: 2rem;
  margin: 0.5rem 0 0.5rem 0;
  transition: 0.1s;
}
.SiderButton {
  color: rgb(40, 40, 40);
  background-color: azure;
  border: 2px solid powderblue;
  box-shadow: 5px 5px 5px rgba(0, 200, 255, 0.5);
  border-radius: 5px;
  font-size: 2rem;
  margin: 0.5rem 0 0.5rem 0;
  transition: 0.1s;
}
.dark-SiderButton:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.SiderButton:hover {
  cursor: pointer;
  transform: scale(1.1);
}
