/* 输入框 */
.UploadInput {
  background-color: lightcyan;
  border: 2px solid powderblue;
}
.dark-UploadInput {
  background-color: rgb(236, 254, 254);
  border: 2px solid lightgray;
}
.UploadInputSize {
  width: 6rem;
  height: 1.5rem;
  border-radius: 5px 0 0 5px;
  border-right: none;
}
.UploadInputSize:focus {
  outline: none;
  background-color: snow;
}
/* 按钮 */
.UploadDownButton {
  background-color: azure;
  border: 2px solid powderblue;
}
.dark-UploadDownButton {
  background-color: dimgray;
  border: 2px solid lightgray;
}
.UploadDownButtonSize {
  padding: 0;
  width: 1.5rem;
  height: 1.5rem;
  border-left: none;
  border-radius: 0 5px 5px 0;
  transition: 0.2s;
}
.UploadDownButtonSize:hover {
  cursor: pointer;
  font-size: 1.2rem;
}

/* 组件列表 */
.UploadDownList {
  font-weight: bold;
  color: rgb(24, 24, 24);
}
.UploadDownListSize {
  position: absolute;
  left: 23%;
  top: 98%;
  background-color: rgba(169, 169, 169, 0.25);
  backdrop-filter: blur(2px);
  z-index: 2;
}
/* 组件列表元素 */
.UploadDownItemSize {
  padding: 0 3px 0 3px;
  font-size: 1.1rem;
}
.UploadDownItem:hover {
  cursor: pointer;
  background-color: lightgray;
}
.dark-UploadDownItem:hover {
  cursor: pointer;
  background-color: darkgray;
}
.UploadDownItemSize:hover {
  cursor: pointer;
}
/* modal对话框确认按钮 */
.UploadModalButtonSize {
  font-weight: bold;
  margin: 0 0.5rem 0 0.5rem;
  transition: 0.2s;
  background-color: dodgerblue;
  border-radius: 5px;
  border-style: none;
  font-size: 1rem;
  padding: 3px 10px 3px 10px;
}
.UploadModalButtonSize:hover {
  cursor: pointer;
  transform: scale(1.1);
}
/* 清除上传框按钮 */
.UploadClearButtonSize {
  margin-left: 2rem;
  border-radius: 5px;
  transition: 0.2s;
}
.UploadClearButtonSize:hover {
  cursor: pointer;
  transform: scale(1.1);
}
/* 上传组件 */
.UploadComponentSize {
  margin-left: 2rem;
  margin-top: 1rem;
}
