/* 节点组的container */
.Sing2catProxiesGroup {
  background-color: ghostwhite;
  border-radius: 15px;
  border: 2px solid powderblue;
  box-shadow: 5px 5px 5px rgba(0, 200, 255, 0.5);
}
.dark-Sing2catProxiesGroup {
  background-color: rgb(60 60 60);
  border-radius: 15px;
  border: 2px solid lightgray;
  box-shadow: 5px 5px 5px rgba(200, 200, 200, 0.5);
}
.Sing2catProxiesGroupSize {
  width: 88%;
  max-height: 18vh;
  margin: 10px;
  overflow-y: auto;
  padding: 10px;
}
/* 代理组的内容 */
.Sing2catProxiesGroupContentSize {
  display: flex;
  flex-wrap: wrap;
}
/* 代理的卡片内容 */
#Sing2catProxiesGroupCardselected {
  background-color: dodgerblue;
}
.dark-Sing2catProxiesGroupCard {
  background-color: dimgray;
  border-radius: 5px;
  border: 2px solid lightgray;
  padding: 2px;
}
.Sing2catProxiesGroupCard {
  background-color: azure;
  border-radius: 5px;
  border: 2px solid powderblue;
  padding: 2px;
}
.Sing2catProxiesGroupCardSize {
  position: relative;
  height: auto;
  font-size: 0.8rem;
  width: 5rem;
  margin: 5px;
  transition: 0.2s;
}
.Sing2catProxiesGroupCardSize:hover {
  cursor: pointer;
  transform: scale(1.1);
}
/* 代理的卡片延迟信息 */
.Sing2catProxiesGroupCardLatencySize {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 0.6rem;
  color: rgb(0, 255, 64);
}
.Sing2catProxiesGroupCardUDPSize {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 0.6rem;
  font-weight: bold;
  color: darkslategrey;
}
/* 节点组的头部标题 */
.Sing2catProxiesGroupHeadSize {
  display: flex;
  font-size: 1.2rem;
  font-weight: bold;
}
/* 节点组的头部按钮 */
.Sing2catProxiesGroupHeadButton {
  background-color: transparent;
  border-radius: 50%;
  border: 2px solid powderblue;
}
.dark-Sing2catProxiesGroupHeadButton {
  background-color: transparent;
  border-radius: 50%;
  border: 2px solid lightgray;
  color: aliceblue;
}
.Sing2catProxiesGroupHeadButtonSize {
  margin-left: 10px;
  transition: 0.2s;
  font-size: 0.8rem;
}
.Sing2catProxiesGroupHeadButtonSize:hover {
  cursor: pointer;
  background-color: dodgerblue;
}
/* 修改时间按钮 */
.Sing2catIntervalButton {
  width: auto;
  border-radius: 5px;
  background-color: dodgerblue;
  border: 2px solid powderblue;
  transition: 0.2s;
}
.Sing2catIntervalButton:hover {
  cursor: pointer;
  transform: scale(1.1);
}
/* 修改时间输入框 */
.Sing2catIntervalInput {
  width: 1.8rem;
}
