/* 搜索输入框 */
.ComponentInput {
  background-color: lightcyan;
  border: 2px solid powderblue;
}
.dark-ComponentInput {
  background-color: rgba(255, 255, 255, 0.6);
  border: 2px solid lightgray;
}
.ComponentInputSize {
  width: 10rem;
  border-right: none;
  border-radius: 5px 0 0 5px;
  font-size: 1.2rem;
  height: 2rem;
}
.ComponentInputSize:focus {
  outline: none;
  background-color: snow;
}
/* 搜索按钮 */
.ComponentButton {
  background-color: azure;
  border: 2px solid powderblue;
}
.dark-ComponentButton {
  color: aliceblue;
  background-color: dimgray;
  border: 2px solid lightgray;
}
.ComponentButtonSize {
  padding: 3px;
  height: 2rem;
  font-size: 1rem;
  border-radius: 0 5px 5px 0;
  border-left: none;
  transition: 0.2s;
}
.ComponentButtonSize:hover {
  cursor: pointer;
  font-size: 1.2rem;
}
/* 控制侧边栏按钮 */
.ComponentControlButton {
  background-color: azure;
  border: 2px solid powderblue;
}
.dark-ComponentControlButton {
  background-color: dimgray;
  border: 2px solid lightgray;
  color: aliceblue;
}
.ComponentControlButtonSize {
  margin: 0 0.5rem 0 0;
  font-size: 1.2rem;
  height: 2rem;
  border-radius: 5px;
  transition: 0.2s;
}
.ComponentControlButtonSize:hover {
  cursor: pointer;
  transform: scale(1.1);
}
/* 展示组件信息的卡片 */
.ComponentSizeCard {
  color: rgba(50, 50, 50, 0.8);
  background-color: azure;
  border-radius: 10px;
  border: 2px solid powderblue;
}
.dark-ComponentSizeCard {
  background-color: dimgray;
  border-radius: 10px;
  border: 2px solid lightgray;
}
.ComponentSizeCardSize {
  display: flex;
  margin-top: 10px;
  font-size: 1.5rem;
  padding: 5px;
  align-items: center;
  transition: 0.2s;
}
.ComponentSizeCardSize:hover {
  cursor: pointer;
  transform: scale(1.1);
}
/* 侧边栏的删除按钮 */
.ComponentSideDelete {
  position: absolute;
  right: -5px;
  bottom: -5px;
  background-color: rgba(60, 60, 60, 0.2);
  backdrop-filter: blur(2px);
  border-radius: 5px;
  transition: 0.2s;
}
.ComponentSideDelete:hover {
  cursor: pointer;
  transform: scale(1.1);
}
