.UserForm {
  width: 13rem;
  background-color: azure;
  border: 2px solid powderblue;
  box-shadow: 5px 5px 5px rgba(0, 200, 255, 0.5);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 10px 10px 10px;
}
.dark-UserForm {
  width: 13rem;
  background-color: dimgray;
  border: 2px solid lightgray;
  box-shadow: 5px 5px 5px rgba(150, 150, 150, 0.5);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 10px 10px 10px;
}
.UserInputzone {
  margin: 0 0 1rem 0;
}
.Userlabels {
  font-size: 0.8rem;
}
.UserInput {
  font-size: 1rem;
  outline: none;
  border-radius: 5px;
  background-color: lightcyan;
  border: 1px solid lightblue;
  padding-left: 0.5rem;
}
.dark-UserInput {
  outline: none;
  font-size: 1rem;
  border-radius: 5px;
  background-color: gainsboro;
  border: 1px solid lightblue;
  padding-left: 0.5rem;
}
.dark-UserInput:focus {
  outline: none;
  border-radius: 5px;
  background-color: ghostwhite;
  border: 2px solid lightblue;
  padding-left: 0.5rem;
}
.UserInput:focus {
  outline: none;
  border-radius: 5px;
  background-color: ghostwhite;
  border: 2px solid lightblue;
  padding-left: 0.5rem;
}
.UserLink {
  color: lightblue;
  text-decoration: none;
}
#right-UserLink {
  position: absolute;
  right: 0;
}
#left-UserLink {
  position: absolute;
  left: 0;
}
.UserButton {
  width: 100%;
  margin-top: 1.5rem;
  padding: 2px 10px 2px 10px;
  border: 2px solid lightcyan;
  border-radius: 15px;
  font-size: 1.5rem;
  color: aliceblue;
  background-color: dodgerblue;
  transition: 0.3s;
}
.UserButton:hover {
  cursor: pointer;
  transform: scale(1.05);
}
