.Head-DarkBright {
  position: absolute;
  right: 5px;
  top: 0;
  background-color: transparent;
  border-style: none;
  height: 2rem;
  font-size: 1.2rem;
  margin-top: 3px;
  color: rgb(40, 40, 40);
}
.dark-Head-DarkBright {
  position: absolute;
  right: 5px;
  top: 0;
  background-color: transparent;
  border-style: none;
  height: 2rem;
  font-size: 1.2rem;
  margin-top: 3px;
  color: aliceblue;
}
.Head-DarkBright:hover {
  cursor: pointer;
  background-color: rgba(105, 105, 105, 0.5);
  border-radius: 5px;
  border-style: none;
  color: rgb(40, 40, 40);
}
.dark-Head-DarkBright:hover {
  cursor: pointer;
  background-color: rgba(105, 105, 105, 0.5);
  border-radius: 5px;
  border-style: none;
  color: aliceblue;
}
.bi {
  transition: color 0.3s ease;
}
