.Calender-container {
  background-color: azure;
  border-radius: 15px;
  border: 1px solid powderblue;
  box-shadow: 5px 5px 5px rgba(0, 200, 255, 0.5);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
}
.dark-Calender-container {
  background-color: dimgray;
  border-radius: 15px;
  border: 1px solid lightgray;
  box-shadow: 5px 5px 5px rgba(150, 150, 150, 0.5);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
}
.Calender-Head {
  margin: 0.5rem;
}
.dark-Calender-Head {
  margin: 0.5rem;
}
.Calender-Head i:hover {
  cursor: pointer;
  color: lightblue;
}
.dark-Calender-Head i:hover {
  cursor: pointer;
  color: aliceblue;
}
.Calender-weekday {
  border: 1px solid rgba(173, 216, 230, 0.3);
}
.dark-Calender-weekday {
  border: 1px solid rgba(211, 211, 211, 0.3);
}
.Calender-normal {
  border-top: 1px solid rgba(173, 216, 230, 0.3);
}
.dark-Calender-normal {
  border-top: 1px solid rgba(211, 211, 211, 0.3);
}
.Calender-select {
  border: 1px solid rgba(173, 216, 230, 0.3);
  background-color: rgb(0, 150, 255);
  border-radius: 10px;
}
.dark-Calender-select {
  border-top: 1px solid rgba(211, 211, 211, 0.3);
  background-color: rgb(0, 150, 255);
  border-radius: 10px;
}
.Head-WeatherContainer {
  width: 16rem;
  height: auto;
  background-color: azure;
  border: 2px solid powderblue;
  box-shadow: 5px 5px 5px rgba(0, 200, 255, 0.5);
  border-radius: 15px;
  color: rgb(40, 40, 40);
}
.dark-Head-WeatherContainer {
  width: 16rem;
  height: auto;
  background-color: dimgray;
  border: 2px solid lightgray;
  box-shadow: 5px 5px 5px rgba(150, 150, 150, 0.5);
  border-radius: 15px;
  color: aliceblue;
}
.Head-WeatherCardContainer {
  display: flex;
  flex-direction: row;
  width: 98%;
  overflow-x: auto;
}
.dark-Head-WeatherCardContainer {
  display: flex;
  flex-direction: row;
  width: 98%;
  overflow-x: auto;
}
.Head-WeatherCardContainer::-webkit-scrollbar {
  display: none;
}
.dark-Head-WeatherCardContainer::-webkit-scrollbar {
  display: none;
}
.Head-WeatherCard {
  flex: 0 0 4rem;
  background-color: ghostwhite;
  margin: 0 5px 5px 5px;
  border-radius: 15px;
  border: 1px solid powderblue;
  text-align: center;
  height: auto;
  -webkit-touch-callout: none; /* iOS Safari */
