.Foot-developer {
  margin: 0;
  font-size: 0.7rem;
}
.Foot-divider {
  position: relative;
}
.dark-Foot-divider {
  position: relative;
}
.Foot-divider::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 5px;
  height: 1px;
  width: calc((100% - var(--child-width)) / 2 - 20px);
  border-top: 1.5px rgba(105, 105, 105, 0.5) solid;
}
.dark-Foot-divider::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 5px;
  height: 1px;
  width: calc((100% - var(--child-width)) / 2 - 20px);
  border-top: 1.5px rgba(185, 185, 185, 0.5) solid;
}
.Foot-divider::after {
  content: "";
  position: absolute;
  top: 50%;
  left: calc((100% + var(--child-width)) / 2 + 15px);
  height: 2px;
  width: calc((100% - var(--child-width)) / 2 - 20px);
  border-top: 1.5px rgba(105, 105, 105, 0.5) solid;
}
.dark-Foot-divider::after {
  content: "";
  position: absolute;
  top: 50%;
  left: calc((100% + var(--child-width)) / 2 + 15px);
  height: 2px;
  width: calc((100% - var(--child-width)) / 2 - 20px);
  border-top: 1.5px rgba(185, 185, 185, 0.5) solid;
}

.Foot-links {
  text-decoration: none;
  margin: 0 3px 0 3px;
  color: rgb(40, 40, 40);
}
.dark-Foot-links {
  text-decoration: none;
  margin: 0 3px 0 3px;
  color: aliceblue;
}
.Foot-pic {
  background-color: snow;
  height: 105px;
  width: 210px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
.dark-Foot-pic {
  background-color: rgb(38, 38, 38);
  height: 105px;
  width: 210px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
