.HomeSelectGroup:hover {
  cursor: default;
}
.HomeSelectGroup {
  font-weight: bold;
}
.dark-HomeSelectGroup:hover {
  cursor: default;
}
.dark-HomeSelectGroup {
  font-weight: bold;
}
.HomeSelect {
  background-color: lightcyan;
  border: 2px solid powderblue;
}
.dark-HomeSelect {
  background-color: dimgray;
  border: 2px solid lightgray;
}
.HomeSelectEle:hover {
  background-color: lightskyblue;
}
.dark-HomeSelectEle:hover {
  background-color: lightsteelblue;
}
.HomeSearch {
  background-color: azure;
  border: 2px solid powderblue;
}
.dark-HomeSearch {
  background-color: lightgray;
  border: 2px solid lightgray;
}
.HomeSearch:focus {
  background-color: snow;
}
.dark-HomeSearch:focus {
  background-color: snow;
}
.HomeSearchButton {
  border: 2px solid powderblue;
  background-color: lightcyan;
}
.dark-HomeSearchButton {
  border: 2px solid lightgray;
  background-color: dimgray;
  color: aliceblue;
}
.HomeLinkCard {
  border-radius: 5px;
  border: 2px solid powderblue;
  background-color: lightcyan;
}
.dark-HomeLinkCard {
  border-radius: 5px;
  border: 2px solid lightgray;
  background-color: rgba(240, 248, 255, 0.5);
}
.HomeContainer {
  width: 100%;
}
.HomeForm {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  max-height: 3rem;
}
.HomeSelectSize {
  position: relative;
  text-align: left;
  width: 7.5rem;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 10px 0 0 10px;
  padding-left: 5px;
  border-right: none;
}
.HomeSelectSize:hover {
  cursor: pointer;
}
.HomeSelectOptSize {
  position: absolute;
  width: 4.5rem;
  font-size: 1.2rem;
  padding: 0;
  text-align: center;
  height: 6rem;
  overflow-y: auto;
  border-top: none;
  z-index: 1;
  background-color: rgba(240, 248, 255, 0.2);
  backdrop-filter: blur(2px);
}
.HomeSelectOptSize::-webkit-scrollbar {
  display: none;
}
.HomeSelectEleSize {
  font-size: 1rem;
  font-weight: normal;
  transition: 0.2s;
}
.HomeSelectEleSize:hover {
  cursor: pointer;
}
.HomeSearchSize {
  width: 20rem;
  border-left: none;
  border-right: none;
}
.HomeSearchSize:focus {
  outline: none;
}
.HomeSearchButtonSize {
  border-radius: 0 10px 10px 0;
  border-left: none;
  transition: 0.2s;
}
.HomeSearchButtonSize:hover {
  font-size: 1.3rem;
  cursor: pointer;
}

.HomeLinkContainer::-webkit-scrollbar {
  display: none;
}
.HomeLinkCardSize {
  padding: 3px;
  width: 3.5rem;
  height: 3rem;
  margin: 2px 5px 0 5px;
  position: relative;
  transition: 0.2s;
}
.HomeLinkCardSize:hover {
  transform: scale(1.05);
}
.HomeDelete {
  border-radius: 3px;
  position: absolute;
  right: -5px;
  bottom: -5px;
  background-color: rgba(128, 128, 128, 0.2);
  backdrop-filter: blur(2px);
  padding: 1px;
  transition: 0.2s;
}
.HomeDelete:hover {
  cursor: pointer;
  transform: scale(1.2);
}
.HomeDeletePop {
  border-radius: 5px;
  background-color: azure;
}
.dark-HomeDeletePop {
  background-color: dimgray;
  border-radius: 5px;
}
@media screen and (min-width: 1201px) {
  .HomeLinkContainer {
    display: flex;
    width: 17rem;
    overflow-x: auto;
    margin-top: 2rem;
    height: 5rem;
    margin-left: 40%;
  }
}
@media screen and (min-width: 351px) and (max-width: 600px) {
  .HomeLinkContainer {
    display: flex;
    width: 17rem;
    overflow-x: auto;
    margin-top: 2rem;
    height: 5rem;
    margin-left: 4%;
  }
}
@media screen and (min-width: 601px) and (max-width: 1200px) {
  .HomeLinkContainer {
    display: flex;
    width: 17rem;
    overflow-x: auto;
    margin-top: 2rem;
    height: 5rem;
    margin-left: 32%;
  }
}
